<script setup>
import { useRoute, useRouter } from 'vue-router';
import axios from 'axios';
import { ref } from 'vue';
import { MD5 } from 'crypto-js';
import { onBeforeRouteUpdate } from 'vue-router'


function logOut() {
    location.href = "https://seiya.mio.ski/logout?redirect_uri=" + location.href
}

const route = useRoute();


const metaTitle = ref(route.meta.title);

onBeforeRouteUpdate((to, from, next) => {
    metaTitle.value = to.meta.title;
    next()
})
// const childMetaInfo = router.matched[0].meta; // meta
const LoginTrue = ref(false);

const uid = ref(null);
const avatar = ref("https://s1.lolis.fyi/seiya/avatar/default.webp");
const nickname = ref("");
const username = ref("");
const sign = ref("");
const gender = ref("");


const GenderIconList = {
    "0": '<div class="w-[1.2rem] h-[1.2rem] mb-0.5 p-0.5 mx-1 border bg-pink-400 rounded-full flex justify-center items-center"><svg xmlns="http://www.w3.org/2000/svg"width="16"height="16"fill="#fff"viewBox="0 0 16 16"><path fill-rule="evenodd"d="M8 1a4 4 0 1 0 0 8 4 4 0 0 0 0-8zM3 5a5 5 0 1 1 5.5 4.975V12h2a.5.5 0 0 1 0 1h-2v2.5a.5.5 0 0 1-1 0V13h-2a.5.5 0 0 1 0-1h2V9.975A5 5 0 0 1 3 5z"/></svg></div>',
    "1": '<div class="w-[1.2rem] h-[1.2rem] mb-0.5 p-0.5 mx-1 border bg-blue-400 rounded-full flex justify-center items-center"><svg xmlns="http://www.w3.org/2000/svg"width="16"height="16"fill="#fff"viewBox="0 0 16 16"><path fill-rule="evenodd"d="M9.5 2a.5.5 0 0 1 0-1h5a.5.5 0 0 1 .5.5v5a.5.5 0 0 1-1 0V2.707L9.871 6.836a5 5 0 1 1-.707-.707L13.293 2H9.5zM6 6a4 4 0 1 0 0 8 4 4 0 0 0 0-8z"/></svg></div>'
}


const token = localStorage.getItem("UIMToken");
function ApiReload() {
    axios.post(
    "https://api.mioseiya.com/app/account/web/get/info", null,
    {
        headers: {
            Authorization: `UIMAC ${token}`
        }
    }
)
    // eslint-disable-next-line no-unused-vars
    .then(response => {
        // console.log(response.data);
        const ApiData = response.data
        uid.value = ApiData["uid"].toString()
        avatar.value = "https://s1.lolis.fyi/seiya/avatar/" + MD5(uid.value).toString()
        // bgimg.value = "background-image: url(https://s1.lolis.fyi/seiya/banner/" + MD5(uid.value).toString() + ");"
        sign.value = ApiData["sign"]
        nickname.value = ApiData["nickname"]
        username.value = ApiData["username"]
        gender.value = ApiData["gender"]
        LoginTrue.value = true;
    })
    // eslint-disable-next-line no-unused-vars
    .catch(error => {
        LoginTrue.value = false;
    });
}
ApiReload()

</script>


