<template>
    <div class="flex flex-col lg:flex-row">
        <div id="danmuList" class="bg-black relative overflow-hidden rounded-lg flex-grow w-full h-[38rem] flex justify-center items-center max-h-[38rem]">
            
            <video :class="LiveStart? 'hidden':'hidden'" ref="videoElement" class="video-js w-full h-ful relative" :style="videoStyle"></video>
            <div @contextmenu.prevent @mousedown.prevent @mouseup.prevent @dblclick="TogglePlayer" class="z-30 absolute w-full h-full top-0 left-0 flex flex-col justify-between items-center">
                <div></div>
                <div>
                    <div v-if="!LiveOpen && LiveStart" class="mb-4 flex flex-col justify-center items-center">
                        <img src="@/assets/images/unstart.png" class="w-20 h-auto" alt="">
                        <div class="text-xl text-white mt-2">()</div>
                    </div>
                    <LiveStop v-if="!LiveStart" />
                </div>
                <div></div>
            </div>
        </div>
        <div class="border flex flex-col justify-between p-3 bg-white bg-opacity-95 rounded-lg w-80 mt-8 lg:mt-0 ml-8 max-h-[38rem]">
            <div>
                <div class="flex justify-between">
                    <div class="text-xs font-medium text-pink-500">(*/*)</div>
                    <div class="text-xs font-medium text-black">: {{ Count }}</div>
                </div>
                <!--  -->
                <div id="LIVEmsgList" class="mt-2 max-h-[28rem] overflow-y-auto">
                    <div class="text-gray-700 text-sm my-0.5"><span class="text-gray-400"><img class="h-[0.875rem] mb-[0.34rem] rounded-sm inline" src="@/assets/images/master.png"></span>: (  )</div>
                    
                </div>
                <!--  -->
            </div>            
            <div class="flex flex-col">
                <textarea @keydown="handleKeyDown" v-model="LiveMsg" type="text" maxlength="40" class="resize-none w-full h-16 p-2 outline-0 ring-0 border rounded-md" placeholder="~" name="" id="" />
                <div class="w-full flex mt-2 justify-end">
                    <button @click="SendLiveMsg" class="w-20 h-7 text-white text-sm font-medium rounded-lg bg-pink-500 hover:bg-pink-700 active:bg-pink-600"></button>

                </div>
            </div>
        </div>
    </div>
</template>


<script setup>
import LiveStop from '@/components/LiveCom/LiveStop.vue'

import { sendMsg } from '@/components/LiveCom/sendMsg'
import { sendError } from '@/components/LiveCom/sendError'
import { sendDanmu } from '@/components/LiveCom/sendDanmu'
import { gSendMsg } from '@/components/gSendMsg'
import axios from 'axios';
import { onMounted,onBeforeUnmount,ref } from 'vue';
import videojs from 'video.js';
import 'video.js/dist/video-js.css';

const LiveStart = ref(true);
const LiveOpen = ref(true);

const player = ref(null);
const videoElement = ref(null);
onMounted(() => {
    player.value = videojs(videoElement.value, {
        controls: false,
        autoplay: true,
        controlBar: false,
        sources: [{
            src: 'http://live.miochyan.net/live/chyantest.m3u8',
            type: 'application/x-mpegURL'
        }]
    });
    player.value.on('playing', () => {
        LiveOpen.value = true;
    })
    player.value.on('loadedmetadata', () => {
        LiveStart.value = true;
    });

    player.value.on('error', () => {
        LiveStart.value = false;
    });


});



function TogglePlayer() {
    if (LiveStart.value) {
        if (LiveOpen.value == false){
            player.value.play();
            LiveOpen.value = true;
        }else {
            player.value.pause();
            LiveOpen.value = false;
        }
    }
}


  // 
onBeforeUnmount(() => {
    player.value.dispose();
});










// WS
const Count = ref(0);
const LiveMsg = ref();

onMounted(() => {


//  WebSocket  WebSocket  URL
const socket = new WebSocket('wss://api.miochyan.net/live/websockt/ws');

// 
socket.onopen = function() {
    console.log('WebSocket ');
    setInterval(ping(socket),1000)
};

// 
socket.onmessage = function(event) {
    console.log(':', event.data);
    // const WsData = JSON.parse(event.data);
    const WsData = eval('(' + event.data + ')');
    if (WsData["type"] === "send") {
        sendMsg("LIVEmsgList",WsData)
        sendDanmu("danmuList",WsData["send"])

    }
    if (WsData["type"] === "count") {
        Count.value = WsData["count"]
    }
};

// 
socket.onclose = function(event) {
    sendError("LIVEmsgList","WebSocket ",`#${event.code}:${event.reason}`)
};

// 
socket.onerror = function(error) {
    sendError("LIVEmsgList","WebSocket ",error)
};

})


function handleKeyDown(event) {
    if (event.ctrlKey && event.key === 'Enter') {
        SendLiveMsg();
    }
}

const token = localStorage.getItem("UIMToken");
LiveMsg.value = ""
function SendLiveMsg() {
    axios.post(
    "https://api.miochyan.net/live/websockt/send", 
    {
        "msg": LiveMsg.value
    },
    {
        headers: {
            Authorization: `UIMAC ${token}`
        }
    }
).then(() => {
    LiveMsg.value = ""
})
    .catch(() => {
        gSendMsg("linMsgList","error","(  )",3000)
    });
}


function ping(ws) {
    ws.send("ping")
}
</script>